import React, { useState } from 'react';

const CentreServicesLayer = () => {
  const [services, setServices] = useState([
    { id: 1, category: 'OPD Consultation Services', name: 'Orthodontist' },
    { id: 2, category: 'Vaccination Services', name: 'Chickenpox Vaccine' },
    { id: 3, category: 'Laboratory & Diagnostic Services', name: 'Dengue Fever Panel' },
  ]);

  const handleDelete = (id) => {
    const confirmDelete = window.confirm('Are you sure you want to delete this service?');
    if (confirmDelete) {
      setServices((prev) => prev.filter((s) => s.id !== id));
    }
  };

  return (
    <div className="dashboard-body__content">

      <div className="card common-card border border-gray-five">
        <div className="card-body">
          {services.length > 0 ? (
            <div className="table-responsive">
              <table className="table text-body">
                <thead>
                  <tr>
                    <th>SN.</th>
                    <th>Category</th>
                    <th>Service Name</th>
                    <th>Action</th>
                  </tr>
                </thead>
                <tbody>
                  {services.map((service, index) => (
                    <tr key={service.id}>
                      <td>{index + 1}</td>
                      <td>{service.category}</td>
                      <td>{service.name}</td>
                      <td>
                        <button
                          onClick={() => handleDelete(service.id)}
                          style={{
                            backgroundColor: 'red',
                            color: 'white',
                            border: 'none',
                            borderRadius: '4px',
                            padding: '5px 10px',
                          }}
                        >
                          Delete
                        </button>
                      </td>
                    </tr>
                  ))}
                </tbody>
              </table>
            </div>
          ) : (
            <p className="text-muted">No services added yet.</p>
          )}
        </div>
      </div>
    </div>
  );
};

export default CentreServicesLayer;
